<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue';

// 查看大图展示相关变量
const showLargeImage = ref(false); // 控制大图显示
const largeImageIndex = ref(0);   // 当前显示的大图索引

function copyCommand() {
    const commandText = "git clone https://github.com/xiaoce-2025/QtCalendar.git";

    // 降级实现
    const copyFallback = () => {
        const textarea = document.createElement('textarea');
        textarea.value = commandText;
        textarea.style.position = 'fixed';
        textarea.style.opacity = '0';
        document.body.appendChild(textarea);
        textarea.select();

        try {
            document.execCommand('copy');
            return true;
        } catch (err) {
            console.warn('传统复制失败', err);
            return false;
        } finally {
            document.body.removeChild(textarea);
        }
    };

    // 现代API检测
    const copyWithModernAPI = async () => {
        if (!navigator.clipboard) return false;

        try {
            await navigator.clipboard.writeText(commandText);
            return true;
        } catch (err) {
            console.warn('剪贴板API失败', err);
            return false;
        }
    };

    // 执行复制并处理结果
    const handleCopyResult = (success) => {
        const btn = document.querySelector('.copy-btn');
        if (!btn) return;

        const originalText = btn.innerHTML;
        btn.innerHTML = success
            ? '<i class="fas fa-check"></i> 已复制'
            : '<i class="fas fa-exclamation-triangle"></i> 复制失败';

        setTimeout(() => {
            btn.innerHTML = originalText;
        }, 2000);
    };

    // 尝试现代API -> 回退传统方式
    (async () => {
        const success = await copyWithModernAPI() || copyFallback();
        handleCopyResult(success);
    })();
}

// 图片自动轮播部分
const currentSlide = ref(0);
const slides = [
    { id: 1, image: "pictures/Yanxx_Calendar/1.png" ,url:"pictures/Yanxx_Calendar/1.png"},
    { id: 2, image: "pictures/Yanxx_Calendar/2.png" ,url:"pictures/Yanxx_Calendar/2.png"},
    { id: 3, image: "pictures/Yanxx_Calendar/3.png" ,url:"pictures/Yanxx_Calendar/3.png"},
    { id: 4, image: "pictures/Yanxx_Calendar/4.png" ,url:"pictures/Yanxx_Calendar/4.png"},
    { id: 5, image: "pictures/Yanxx_Calendar/5.png" ,url:"pictures/Yanxx_Calendar/5.png"},
    { id: 6, image: "pictures/Yanxx_Calendar/6.png" ,url:"pictures/Yanxx_Calendar/6.png"},
    { id: 7, image: "pictures/Yanxx_Calendar/7.png" ,url:"pictures/Yanxx_Calendar/7.png"},
    { id: 8, image: "pictures/Yanxx_Calendar/8.png" ,url:"pictures/Yanxx_Calendar/8.png"},
    { id: 9, image: "pictures/Yanxx_Calendar/9.png" ,url:"pictures/Yanxx_Calendar/9.png"},
]

// 切换幻灯片
const nextSlide = () => {
    currentSlide.value = (currentSlide.value + 1) % slides.length;
};

const prevSlide = () => {
    currentSlide.value = (currentSlide.value - 1 + slides.length) % slides.length;
};

// 自动轮播
let intervalId = null;

onMounted(() => {
    intervalId = setInterval(() => {
        nextSlide();
    }, 5000);
});

onBeforeUnmount(() => {
    clearInterval(intervalId);
});

// 查看大图相关函数
// 图片打开方法
const openLargeImage = (index) => {
    largeImageIndex.value = index;
    showLargeImage.value = true;
};

// 新增的关闭方法
const closeLargeImage = () => {
    showLargeImage.value = false;
};

// 大图导航方法
const navigateLargeImage = (direction) => {
    if (direction === 'prev') {
        largeImageIndex.value = (largeImageIndex.value - 1 + slides.length) % slides.length;
    } else if (direction === 'next') {
        largeImageIndex.value = (largeImageIndex.value + 1) % slides.length;
    }
};

// 无法下载提示
const showDownloadAlert = () => {
  alert("由于服务器带宽限制，暂时无法提供下载");
};
</script>


