body {
    font-family: 'Noto Sans SC', sans-serif;
    color: var(--dark);
    background-color: #f0f4f9;
    line-height: 1.6;
}

.container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 20px;
}

/* 项目内容区域 */
.project-content {
    display: flex;
    gap: 30px;
    margin-bottom: 30px;
}

/* 左侧区域 (视频 + 功能) */
.video-section {
    flex: 7;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.video-area {
    border-radius: 12px;
    padding: 30px;
    margin-bottom: 30px;
    box-shadow: var(--shadow);
    background: linear-gradient(45deg, rgba(255, 179, 0, 0.1), rgba(255, 193, 7, 0.2));
}

/* 项目标题栏 */
.project-title-bar {
