}

.project-title {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 15px;
}

.project-title h1 {
    font-size: 28px;
    font-weight: 700;
    color: var(--dark);
}

.project-tag {
    background-color: var(--accent);
    color: white;
    padding: 5px 12px;
    border-radius: 50px;
    font-size: 14px;
    font-weight: 500;
}

.project-info {
    display: flex;
    gap: 25px;
    align-items: center;
}

.author {
