/* 轮播展示 */
.carousel-section {
    margin: -20px 0;
    padding: 30px 0;
}

.image-carousel {
    position: relative;
    max-width: 900px;
    margin: 0 auto;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
}

.carousel-inner {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;
    /* 16:9 宽高比 */
    overflow: hidden;
    background: linear-gradient(135deg, #ffecd2 0%, #fcb69f 100%);
}

.carousel-slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 1s ease, transform 10s linear;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.carousel-slide.active {
    opacity: 1;
    z-index: 2;
}

.carousel-slide img {
