    transform: scale(1.2);
}

.carousel-nav {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 50px;
    height: 50px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
    z-index: 10;
    backdrop-filter: blur(5px);
    transition: var(--transition);
}

.carousel-nav:hover {
    background: rgba(255, 152, 0, 0.8);
}

.carousel-nav.prev {
    left: 20px;
}

.carousel-nav.next {
