}

.carousel-link:hover .carousel-link-overlay {
    opacity: 1;
}

.carousel-link:hover .carousel-link-overlay i,
.carousel-link:hover .carousel-link-overlay h3,
.carousel-link:hover .carousel-link-overlay p {
